import { Component, OnInit } from '@angular/core';
import { NavigationService } from 'src/app/common/navigation.service';
import * as $ from 'node_modules/jquery/dist/jquery';

@Component({
  selector: 'appHomePageAbout',
  templateUrl: './hp-about.component.html',
  styleUrls: ['./hp-about.component.css']
})
export class HPAboutComponent implements OnInit {

  constructor(
    public nvtService: NavigationService
  ) { }

  ngOnInit() {
    $(window).scroll(function () {
      if ($('#aboutPage').after().offset().top + ($('#aboutPage').after().offset().top * 0.10) > $(this).scrollTop() && $('#aboutPage').after().offset().top - ($('#aboutPage').after().offset().top * 0.10) < $(this).scrollTop()) {
        $("#navMenuAbou").css("display", "flex");
      } else {
        $("#navMenuAbou").css("display", "none");
      }
    });
  }

  homeIdNavigator(navigatedId) {
    this.nvtService.homeIdNavigator(navigatedId);
  }

}
