import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery/dist/jquery';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  constructor() { }

  ngOnInit() {
    console.log($('[id^="cdl0"]'));
    console.log($('[id^="cd0"]'));
  }

  navLinkTabActive(id: any) {
    console.log("ID: " + id);
    $('[id^="cdl0"]').removeClass("active");
    $('[id^="cd0"]').css('display','none');

    $('#cdl'+id).addClass("active");
    $('#cd'+id).css('display','block');
  }

}
