import { Component, OnInit } from '@angular/core';
import { NavigationService } from 'src/app/common/navigation.service';
import * as $ from 'node_modules/jquery/dist/jquery';

@Component({
  selector: 'appHomePageInstitutional',
  templateUrl: './hp-institutional.component.html',
  styleUrls: ['./hp-institutional.component.css']
})
export class HPInstitutionalComponent implements OnInit {

  constructor(
    public nvtService: NavigationService
  ) { }

  ngOnInit() {
    $(window).scroll(function () {
      if ($('#institutionalPage').after().offset().top + ($('#institutionalPage').after().offset().top * 0.10) > $(this).scrollTop() && $('#institutionalPage').after().offset().top - ($('#institutionalPage').after().offset().top * 0.10) < $(this).scrollTop()) {
        $("#navMenuInst").css("display", "flex");
      } else {
        $("#navMenuInst").css("display", "none");
      }
    });
    // se $('#institutionalPage').after().offset().top com 10% de taxa de erro estiver proximo de $(this).scrollTop();
  }

  homeIdNavigator(navigatedId) {
    this.nvtService.homeIdNavigator(navigatedId);
  }

}
